<template>
  <div class="lightbox-comp">

    <button class="btn-design" v-on:click="changePhoto(-1)">‹</button>

    <section>
      <img v-bind:src="photo.urls.regular" 
      v-bind:alt="'Copyright: ' + photo.user.name">
        <!-- Full picture -->
      <p>Copyright {{ photo.user.name }}</p>
    </section>

    <button class="btn-design" v-on:click="changePhoto(1)">›</button>

  </div>
</template>

<script>
export default {
  props: {
    photo: Object
  },
  methods: {
    changePhoto(upOrDown) {
      this.$emit('changePhoto', upOrDown)
    }
  }
}
</script>

<style>
  .lightbox-comp {
    display: flex;
    margin-bottom: 60px;
    justify-content: center;
  }

  .lightbox-comp img {
    padding: 32px;
    border: 4px solid black;
  }
  p {
    height: 20px;
    margin: 20px;
  }

  .btn-design {
      margin-bottom: 60px;
      border: none;
      outline: none;
      background: none;
      font-size: 300px;
      cursor: pointer;
  }
</style>