<template>
  <div class="search-comp">
    <h1>SEARCH FOR PHOTOS</h1>
    <input v-model="searchInput" type="text">
    <button class="search-btn default-btn" v-on:click="searchClick">SEARCH</button>
  </div>
</template>

<script>
import { getPhotos } from '@/api/api.js'

export default {
  data() {
    return {
      searchInput: ""
    }
  },
  methods: {

    taBortMig() {
      let photos2 = ["ett", "två", "tre"];
      this.$emit('foundPhotos', photos2);
    },

    async searchClick() {

      console.log(this.searchInput + 'i searchClick functionen');

      this.$emit('searchInput', this.searchInput);

      const data = await getPhotos(this.searchInput, 1);

      console.log(data);
      let photos = data;

      this.$emit('foundPhotos', photos);
    }
  }
}
</script>

<style>
  .search-comp {
    margin-bottom: 60px;
  }

  h1 {
    font-size: 36px;
    margin-bottom: 36px;
  }

  input {
    font-size: 16px;
    padding: 16px;
    outline: none;
    border: 2px solid black;
    border-radius: 10px 0 0 10px;
    width: 400px;
  }

  .search-btn {
    border-radius: 0 10px 10px 0;
    outline: none;
  }
</style>