import axios from "axios";

const API_URL = "https://api.unsplash.com/";
axios.defaults.baseURL = API_URL;

const AUT_TOKEN = "Client-ID 5hmtYkwRexp_O92bmCWR06phCjIVSrvUZE2GO0USyK0";
axios.defaults.headers.common['Authorization'] = AUT_TOKEN;

export const getPhotos = async (searchInput, page) => {
  try {
    const response = await axios.get(`search/photos?page=${page}&query=${searchInput}`);

    return response.data;
  } catch (error) {
    console.log(error);
  }
}

