<template>
  <div id="app">
    <SearchComp v-on:searchInput="saveInput" v-on:foundPhotos="sendToGallery"/>
    <LightboxComp v-show="reqShow" v-on:changePhoto="changePhoto" v-bind:photo="updateLightbox" />
    <GalleryComp v-bind:foundPhotos="updateGalleryPage" v-on:clickedPhoto="openPhoto" />
    <NavComp v-bind:foundPhotos="tempPhotos" v-bind:currentPage="currentPage" v-on:currentPage="changePage" />
  </div>
</template>

<script>
import SearchComp from './components/SearchComp.vue'
import GalleryComp from './components/GalleryComp.vue'
import LightboxComp from './components/LightboxComp.vue'
import NavComp from './components/NavComp.vue'

import { getPhotos } from '@/api/api.js'

export default {
  name: 'App',
  components: {
    SearchComp,
    GalleryComp,
    LightboxComp,
    NavComp,
  },

  data() {
    return {
      tempPhotos: [],
      tempPhoto: {},
      currentPage: 1,
      searchInput: "",
      pageCache: [],
      reqShow: true
    }
  },

  computed: {
    updateGalleryPage() {
      let update = this.tempPhotos;
        return update
    },
    updateLightbox() {
      let update = this.tempPhoto;
        return update
    },
  },

  methods: {
    sendToGallery(photos) {
      this.tempPhotos = photos;

      let setFirstPhoto = this.tempPhotos.results[0]
      this.tempPhoto = setFirstPhoto;
      this.reqShow = true;
      this.pageCache.push(photos);
    },
    openPhoto(photo) {
      this.tempPhoto = photo;
    },
    saveInput(searchInput) {
      this.searchInput = searchInput;
    },
    changePhoto(upOrDown) {

      let photoID = this.tempPhoto.id;
  
      for (let i = 0; i < this.tempPhotos.results.length; i++) {
        const element = this.tempPhotos.results[i].id;

        if (photoID === element) {

          if(upOrDown === -1 && this.currentPage === 1 && i === 0) {
            alert("gå inte ner under 0")
            return
          }
          if (i === 0 && upOrDown === -1) {
            let currentPage = this.currentPage -1;
            this.changePage(currentPage);
            let newPhoto = this.tempPhotos.results[9];
            this.tempPhoto = newPhoto;
          }
          else if (i === 9 && upOrDown === 1) {
            let currentPage = this.currentPage +1;
            this.changePage(currentPage);
            let newPhoto = this.tempPhotos.results[0];
            this.tempPhoto = newPhoto;
          }
          else {
            let newPhoto = this.tempPhotos.results[i + parseInt(upOrDown)];
            this.tempPhoto = newPhoto;
          }
        }       
      }
    },

    changePage(currentPage) {
      this.reqShow = false;

      this.currentPage = currentPage;

      if (currentPage < this.pageCache.length) {
          this.tempPhotos = this.pageCache[currentPage - 1];
          console.log(this.pageCache[currentPage - 1]);
          this.reqShow = true;
      }
      else {
        this.getPageAPI();
      }
    },

    async getPageAPI() {
      
      const data = await getPhotos(this.searchInput, this.currentPage);
      this.sendToGallery(data);
      this.pageCache.push(data);
    },
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.default-btn {
  background: black;
  color: white;
  font-size: 16px;
  padding: 16px;
  border: 2px solid black;
  cursor: pointer;
}
</style>
