<template>
<div>
    <nav>
        <button class="btn-left default-btn" v-on:click="prevPage">«</button>
        <span v-bind="currentPage"> {{ currentPage }} </span>/
        <span> {{foundPhotos.total_pages}} </span>
        <button class="btn-right default-btn" v-on:click="nextPage">»</button>
    </nav>
</div>
</template>

<script>
export default {

    props: {
        currentPage: Number,
        foundPhotos: Object
    },

    methods: {
    nextPage() {
        this.currentPage++;
        this.$emit('currentPage', this.currentPage);
    },
    prevPage() {
    if (this.currentPage === 1) {
        return    
    }
        this.currentPage--;
        this.$emit('currentPage', this.currentPage);
    }
  }
}
</script>

<style>
    .btn-left {
        border-radius:  10px 0 0 10px;
        font-size: 48px;
    }
    .btn-right {
        border-radius: 0 10px 10px 0;
        font-size: 48px;
    }
    nav {
        background: black;
        color: white;
        font-size:32px;
        padding: 16px;
        border: 3px solid black;
    }
</style>